img.thumbnail {
    /*height: 250px;*/
    /*width: 250px;*/
    height: auto;
    width: 330px;
    margin: 10px;
    cursor: pointer;
    border-radius: 5px;
    display: inline-block;

}
img.ril-image-current.ril__image{
    height: auto;
    width: auto;
}
.image-display{
    text-align: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-item-align: center;
        align-self: center;
}
img{
    display: block;
    width: 100%;
    height: auto;
}



